import React, { useEffect } from "react";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import axios from "axios";
import { userDataGet, fetchUserById } from "../features/counter/UsersDetails";
import { useDispatch, useSelector } from "react-redux";
import UserCard from "./UserInfo";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "80%",
    backGround: "Red",
    height: "400px",
    color: "red",
    margin: "40px auto 50px auto",
    [theme.breakpoints.up("md")]: {
      backgroundColor: "theme.palette.primary.main",
    },
  },

  paper: {
    color: "red",
  },
  card: {
    margin: "20px",
  },
}));

const Fetchuser = () => {
  const { users } = useSelector((state) => state.counter);

  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    
    const getData = async () => {
      const responseData = await axios.get(
        "https://jsonplaceholder.typicode.com/users"
      );
    
      dispatch(userDataGet(responseData?.data));
    };

    if (users.length === 0) {
      getData();
    }
    return;
  }, []);
  console.log(users);
  return (
    <div>
      <Grid container className={classes.root}>
        {users.map((items, idx) => {
          return (
            <div key={idx}>
              <Grid item sm={3} md={2} className={classes.card}>
                <UserCard
                  indexNumber={items.id}
                  items={items}
                  name={items.name}
                  id={items.id}
                  email={items.email}
                  phone={items.phone}
                  website={items.website}
                />
              </Grid>
            </div>
          );
        })}
      </Grid>
    </div>
  );
};
export default Fetchuser;
