import React, { memo, useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import EditIcon from "@material-ui/icons/Edit";
import Typography from "@material-ui/core/Typography";
import DeleteIcon from "@material-ui/icons/Delete";
import SaveIcon from "@material-ui/icons/Save";
import { Input } from "@material-ui/core";
import { Paper } from "@material-ui/core";
import { removeItem } from "../features/counter/UsersDetails";
import { useDispatch } from "react-redux";
import { editUser } from "../features/counter/UsersDetails";

import { useHistory } from "react-router-dom";
const useStyles = makeStyles((theme) => ({
  root: {
    width: "300px",
    height: "290px",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginTop: "10px",
  },
  cardAction: {
    display: "flex",
    justifyContent: "space-between",
    width: "100%",
  },
  detailButton: {
    marginLeft: "100px",
    marginTop: "-62px",
  },
}));
const UserCard = (props) => {
  const historyHook = useHistory();
  const dispatch = useDispatch();
  const [state, setstate] = useState({
    id: "",
    name: "",
    phone: "",
    email: "",
    index: "",
  });

  useEffect(() => {
    setstate({
      id: props.id,
      name: props.name,
      phone: props.phone,
      email: props.email,
      indexNumber: props.indexNumber,
    });
  }, []);
  const classes = useStyles();
  const [edit, setedit] = useState(false);

  const editButton = (e) => {
    e.stopPropagation();
    setedit(true);
    console.log(state);
  };
  const removeUser = (e) => {
    if (window.confirm("are you sure ")) {
      dispatch(removeItem(props.id));
    }
    e.stopPropagation();
  };
  const saveState = (e) => {
    e.stopPropagation();
    setedit(false);
    dispatch(editUser(state));
  };
  const setInput = (e) => {
    setstate({
      ...state,
      [e.target.name]: e.target.value,
    });
    console.log(state);
  };

  const historyPush = () => {
    historyHook.push(`${props.indexNumber}`);
  };
  return (
    <>
      <Card className={classes.root}>
        <CardActions>
          <Paper className={classes.cardAction} elevation={0}>
            <Button size="small" onClick={removeUser}>
              <DeleteIcon color="error" />
            </Button>
            {edit ? (
              <Button>
                <SaveIcon color="primary" onClick={saveState} />
              </Button>
            ) : (
              <Button size="small" onClick={editButton}>
                <EditIcon color="primary" />
              </Button>
            )}
          </Paper>
        </CardActions>
        <CardContent>
          <Typography
            className={classes.title}
            color="textSecondary"
            gutterBottom
          >
            {edit ? (
              <Input
                placeholder="Placeholder"
                defaultValue={state.id}
                name="id"
                onChange={setInput}
              />
            ) : (
              <p>id: {props.id}</p>
            )}
          </Typography>
          <br />
          <Typography variant="h5" component="h2">
            {edit ? (
              <Input
                placeholder="Placeholder"
                defaultValue={state.name}
                disableUnderline={true}
                name="name"
                onChange={setInput}
              />
            ) : (
              <p>Name : {props.name}</p>
            )}
          </Typography>
          <br />
          <Typography className={classes.pos}>
            {edit ? (
              <Input
                placeholder="Placeholder"
                defaultValue={state.phone}
                disableUnderline={true}
                name="phone"
                onChange={setInput}
              />
            ) : (
              <p>Phone : {props.phone}</p>
            )}
          </Typography>

          <Typography variant="body2" component="p">
            {edit ? (
              <Input
                placeholder="Placeholder"
                defaultValue={state.email}
                disableUnderline={true}
                name="email"
                onChange={setInput}
              />
            ) : (
              <p>Email: {props.email}</p>
            )}
          </Typography>
        </CardContent>
      </Card>
      <Button
        className={classes.detailButton}
        size="small"
        color="primary "
        variant="contained"
        onClick={historyPush}
      >
        Details
      </Button>
    </>
  );
};
export default memo(UserCard);
