import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

const initialState = {
  users: [],
  status: "idle",
  detialsITems: "",
};

export const UsersDetails = createSlice({
  name: "counter",
  initialState,
  reducers: {
    userDataGet: (state, action) => {
      // const editUserId = action.payload.map((items, idx) => {
      //   return { ...items };
      // });
      // state.users = editUserId;
      state.users = action.payload;
    },

    removeItem: (state, action) => {
      const remove = state.users.filter((items) => {
        return items.id !== action.payload;
      });
      state.users = remove;
    },

    editUser: (state, action) => {
      console.log(action.payload.id);
      const editedState = state.users.map((items, idx) => {
        return idx === action.payload.indexNumber
          ? { ...action.payload }
          : { ...items };
      });
      state.users = editedState;
    },
    addNewUser: (state, action) => {
      state.users.push(action.payload);
    },
  },
});

export const { userDataGet, removeItem, editUser, addNewUser } =
  UsersDetails.actions;

export default UsersDetails.reducer;
