import React, { useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import { useForm } from "react-hook-form";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import { addNewUser } from "../features/counter/UsersDetails";
import { useDispatch } from "react-redux";
const useStyles = makeStyles((theme) => ({
  root: {
    width: "60%",
    margin: "auto",
    padding: "20px",
    textAlign: "center",
    border: "1px solid #001a00",
  },
  heading: {
    textAlign: "center",

    marginBottom: "50px",
  },

  submitButton: {
    marginTop: theme.spacing(1),
  },
}));

const AddUser = () => {
  const [redirect, setredirect] = useState(false);
  useEffect(() => {
    console.log("from use efefct");
  }, []);

  const classes = useStyles();
  const dispatch = useDispatch();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = (data) => {
    dispatch(addNewUser(data));
    console.log(data);
    setredirect(!redirect);
  };
  return (
    <div>
      <Typography
        variant="h4"
        component="h5"
        align="center"
        color="secondary"
        onSubmit={onSubmit}
        gutterBottom={true}
      >
        Add user
      </Typography>

      <form onSubmit={handleSubmit(onSubmit)}>
        <Grid
          container={true}
          className={classes.root}
          spacing={5}
          border={1}
          alignItems="center"
        >
          <Grid item sm={8} md={6} lg={6}>
            <TextField
              id="ID"
              label="ID"
              variant="outlined"
              fullWidth={true}
              {...register("id", {})}
            />
          </Grid>
          <Grid item sm={8} md={6} lg={6}>
            <TextField
              id="Name"
              label="Name"
              variant="outlined"
              fullWidth={true}
              {...register("name", { required: true, maxLength: 15 })}
            />
          </Grid>
          <Grid item sm={8} md={6} lg={6}>
            <TextField
              id="Phone #"
              label="Phone"
              type="number"
              variant="outlined"
              fullWidth={true}
              {...register("phone", {})}
            />
            <br />
          </Grid>
          <Grid item sm={8} md={6} lg={6}>
            <TextField
              id="Email"
              label="Email"
              variant="outlined"
              fullWidth={true}
              {...register("email", { required: true })}
            />
          </Grid>
          <Grid item sm={8} md={8} lg={12} justify="center">
            <Button
              type="submit"
              variant="contained"
              color="primary"
              className={classes.submitButton}
            >
              Submit form
            </Button>
          </Grid>
        </Grid>
      </form>
      {redirect ? <Redirect to="/" /> : null}
    </div>
  );
};
export default AddUser;
